import uniIcons from '@/components/uni-icons/uni-icons.vue'
export default {
	components: {
		uniIcons
		
	},
	data() {
		return {
			imageList:[],
			name:"",
			idCard:"",
			phone:"",
			 array: ['请选择', '男', '女'],
			   sexIndex: 0,
			   workList:['请选择','政府','国家电网','财务','路政','纪委'],
			   workIndex:0,
		userInfo:{
			avatar:"",
			username:"",
			idcard:"",
			sex:"",
			phone:"",
			unit:""
		}
		};
	},
	onShow(){
	uni.hideHomeButton()
	},
	methods:{
		jumpRegster(){
			uni.redirectTo({
			    url: '../login/login',
				
			});
		},
			
		submitMsg(){
			console.log(this.userInfo)
			
			if(this.userInfo.avatar.length<1){
				uni.showModal({
					
					content: "请上传头像!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.avatar.username<1){
				uni.showModal({
					
					content: "请输入姓名!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.avatar.idcard<1){
				uni.showModal({
					
					content: "请输入身份证号!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.avatar.sex<1){
				uni.showModal({
					
					content: "请选择性别!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.avatar.phone<1){
				uni.showModal({
					
					content: "请输入电话号码!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			if(this.userInfo.avatar.unit<1){
				uni.showModal({
			
					content: "请选择所在单位!",
					showCancel: false,
					confirmText: "确定"
				})
				return
			}
			uni.showLoading({
				title: 'loading'
			});
			//发起请求
			this.$http.post('/interface/rest/http/xlgc/wb-test.htm',this.userInfo).then(res => {
				console.log(res.data.list);
				if(res.code==200){
					uni.hideLoading();
					
					uni.redirectTo({
					    url: '../regres/regres',
						
					});
				}
			}).catch(err => {
				console.log(err);
			})
			uni.redirectTo({
			    url: '../regres/regres',
				
			});
		},
		//上传头像
		chooseImage(){
			uni.chooseImage({
				count:1,
				success: (res) => {
					 console.log(JSON.stringify(res.tempFilePaths));
					this.userInfo.avatar =res.tempFilePaths[0];
				},
				
			})
		},
		//选择单位
		workListChange(e){
			this.workIndex = parseInt(e.detail.value)
			this.userInfo.unit =this.workList[this.workIndex]
		},
			
		sizeTypeChange(e){
			this.sexIndex= parseInt(e.detail.value)
			this.userInfo.sex = parseInt(e.detail.value)==0?"M":"F"
		}
	}
}
